<template>
    <section class="tab padding-t_8" id="tabs">
        <loader :is-visible="isLoading"></loader>
        <div class="container-fluid tab-style">
            <input type="hidden" id="AttachmentName" name="AttachmentName" />
            <div class="tab_container">
                <div class="responsive-tabs-container accordion-xs accordion-sm">
                    <ul class="nav nav-tabs all-tabs responsive-tabs mt-2 responivecustomtab HideTab" id="myTab"
                        role="tablist">
                        <li class="nav-item">
                            <a class="nav-link active productCreateTab1" href="#tabs-1" data-toggle="tab"
                                v-on:click="UserGuideChange('User_Guide_Asset_Catalogue_Add')" role="tab"
                                id="tab-Details"><span class="tab-no">1</span> {{ $t('CreateItem') }}</a>
                        </li>
                        <li class="nav-item">
                            <a class="nav-link gen_icon productDeployTab2" href="#tabs-2"
                            v-on:click="UserGuideChange('User_Guide_Asset_Catalogue_DeployProduct_Tab')" data-toggle="tab"
                                role="tab" id="tab-timeline"><span class="tab-no">2</span> {{ $t('DeployItem') }}</a>
                        </li>
                        <li class="nav-item">
                            <a class="nav-link gen_icon_1" href="#tabs-3"
                            v-on:click="UserGuideChange('User_Guide_Asset_Catalogue_RelatedInfo_Tab')" data-toggle="tab"
                                role="tab" id="tab-timeline"><span class="tab-no">3</span> {{ $t('RelatedInformation') }}
                            </a>
                        </li>
                        <li class="nav-item">
                            <a class="nav-link gen_icon_2" href="#tabs-4"
                            v-on:click="UserGuideChange('User_Guide_Asset_Catalogue_Attachments_Tab')" data-toggle="tab"
                                role="tab" id="tab-timeline"><span class="tab-no">4</span> {{ $t('Attachments') }} </a>
                        </li>
                    </ul>
                    


                </div>

            </div>
            
            
        </div>
        <DeployProduct v-if="IsShowDeployProducts"></DeployProduct>
        <CreateProduct :AssetCatalogId="AssetCatalogId" ref="UpdateProduct" v-if="IsShowCreateProduct"></CreateProduct>
        <RelatedInformation v-if="IsShowRelatedInformation" :chkRelatedInfo="chkRelatedInfo"></RelatedInformation>
        <ShowAttachmentTab ref="UpdateProduct" v-if="IsShowAttachmentTab"></ShowAttachmentTab>
        <div class="col-lg-6 text-right mt-3 float-right">
        </div>
    </section>
    
</template>
<script>
import DeployProduct from '../AssetCatalog/DeployProducts.vue' 
import CreateProduct from '../AssetCatalog/CreateProducts.vue'
import RelatedInformation from '../AssetCatalog/RelatedInformation.vue'
import ShowAttachmentTab from '../AssetCatalog/AttachmentsTab.vue'
import { onBeforeMount } from 'vue'
export default
    {
        components:{
            DeployProduct,
            CreateProduct,
            RelatedInformation,
            ShowAttachmentTab
        },
        data() {
            return {
                IsShowDeployProducts:false,
                IsShowCreateProduct:true,
                IsShowRelatedInformation:false,
                IsShowAttachmentTab:false,
                IsShowbtnPrevious:false,
                IsShowbtnNext:true,
                IsShowbtnSave:true,
                IsShowbtnCancel:true,
                AssetCatalogId:'',
                chkRelatedInfo:false
            }
        },
        created() {
            
            var vm=this;
            vm.AssetCatalogId = window.location.href.split('/')[5]
        },
        methods: {
            btnCancel()
            {
                debugger
                var vm=this;
                vm.$router.push({ path: '/AssetCatalog/Index'});
            },
            UserGuideChange(item)
            {
                
                var vm=this;
                if(item=='User_Guide_Asset_Catalogue_Add')
                {
                    vm.IsShowCreateProduct=true;
                    vm.IsShowDeployProducts=false;
                    vm.IsShowRelatedInformation=false;
                    vm.IsShowAttachmentTab=false;
                }
                if(item=='User_Guide_Asset_Catalogue_DeployProduct_Tab'){
                    vm.IsShowDeployProducts=true;
                    vm.IsShowCreateProduct=false;
                    vm.IsShowRelatedInformation=false;
                    vm.IsShowAttachmentTab=false;
                }
                if(item=='User_Guide_Asset_Catalogue_RelatedInfo_Tab'){
                    debugger
                    vm.IsShowDeployProducts=false;
                    vm.IsShowCreateProduct=false
                    vm.chkRelatedInfo=true;
                    vm.IsShowRelatedInformation=true
                    vm.IsShowAttachmentTab=false;
                }
                if(item=='User_Guide_Asset_Catalogue_Attachments_Tab'){
                    vm.IsShowDeployProducts=false;
                    vm.IsShowCreateProduct=false
                    vm.IsShowRelatedInformation=false;
                    vm.IsShowAttachmentTab=true;
                }

            },
            btnNextTab()
            {
                debugger
                var vm=this;
                var res = vm.getSelectedTabIndex();
                if (res == "#tabs-1" ) {
                    $('.responivecustomtab a[href="#tabs-2"]').trigger('click');
                    vm.IsShowDeployProducts=true;
                    vm.IsShowCreateProduct=false;
                    vm.IsShowRelatedInformation=false;
                    vm.IsShowAttachmentTab=false;
                }
                if(res == "#tabs-2" )
                {
                    $('.responivecustomtab a[href="#tabs-3"]').trigger('click');
                    vm.IsShowDeployProducts=false;
                    vm.IsShowCreateProduct=false
                    vm.IsShowRelatedInformation=true
                    vm.IsShowAttachmentTab=false;
                }
                if(res == "#tabs-3" )
                {
                    $('.responivecustomtab a[href="#tabs-4"]').trigger('click');
                    vm.IsShowDeployProducts=false;
                    vm.IsShowCreateProduct=false
                    vm.IsShowRelatedInformation=false;
                    vm.IsShowAttachmentTab=true;
                }
                
            },
            btnPreviousTab()
            {
                debugger
                var vm=this;
                var res = vm.getSelectedTabIndex();
                if (res == "#tabs-4" ) {
                    $('.responivecustomtab a[href="#tabs-3"]').trigger('click');
                    vm.IsShowDeployProducts=false;
                    vm.IsShowCreateProduct=false
                    vm.IsShowRelatedInformation=true
                    vm.IsShowAttachmentTab=false;
                }
                if(res == "#tabs-3" )
                {
                    $('.responivecustomtab a[href="#tabs-2"]').trigger('click');
                    vm.IsShowDeployProducts=true;
                    vm.IsShowCreateProduct=false;
                    vm.IsShowRelatedInformation=false;
                    vm.IsShowAttachmentTab=false;
                }
                if(res == "#tabs-2" )
                {
                    $('.responivecustomtab a[href="#tabs-1"]').trigger('click');
                    vm.IsShowCreateProduct=true;
                    vm.IsShowDeployProducts=false;
                    vm.IsShowRelatedInformation=false;
                    vm.IsShowAttachmentTab=false;
                }
                
            },
            getSelectedTabIndex() {
                var selectedTab = "";
                $('.responivecustomtab a').each(function (index) {
                    if ($(this).hasClass("active")) {
                        selectedTab = $(this).attr('href');
                    }
                });
                return selectedTab;
            },
            btnSave()
            {
                debugger
                var vm = this;
               vm.$refs.UpdateProduct.UpdateProduct();
           
            }
        }
    }
</script>